.countriesDetails {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 72px;
    padding-bottom: 72px;




}

.countriesDetails img {
    height: 300px;
    width: 400px;
    border-radius: 3px;
    box-shadow: -4px -4px 16px 16px var(--Dark-Blue);
}

.countriesDetails h1 {
    margin-bottom: 25px;
}

.countriesDetails p {
    padding: 5px;

}

.countriesDetails span {
    margin-right: 5px;
}

.btn-border {
    margin: 25px 3px 25px 3px;
    border-radius: 3px;
    padding: 8px 12px 8px 12px;
    border: none;

}